export default {
  name: 'image',
  filter: (node: HTMLElement) => {
    return node.tagName === 'IMG';
  },
  replacement: (content?: string, node?: HTMLElement) => {
    const [name, src] = ['name', 'src'].map((attr) =>
      node?.getAttribute(attr),
    );
    return `![${name || 'image'}](${src || ''})`;
  },
};
